.form {
  width: 100%;
  height: 100%;
  background-color: rgb(250, 250, 250);
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputBox {
  width: 100%;
  display: flex;
  padding: 10px 30px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.input {
  width: 70%;
  height: 30px;
  border: none;
  padding-bottom: 10px;
  background-color: rgb(238, 238, 238);
  border-bottom: 1px solid rgba(39, 39, 39, 0.562);
  outline: none;
}

.input:active {
  border-bottom: 1px solid rgb(83, 188, 230);
}

.input::placeholder {
  color: #fff;
}

.submit {
  margin-top: 20px;
  width: 40%;
  height: 30px;
  color: '#fff';
  background: rgb(138, 134, 134);
}

.container {
  width: 100%;
  height: 100vh;
}

.selectInp {
  width: 70%;
  height: 30px;
  outline: none;
}
