.technologies_item {
  width: 90%;
  margin: 40px auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.item_conteiner {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.addItem {
  display: flex;
  justify-content: center;
  align-items: center;
}
.technologytBTN {
  font-size: 75px;
  color: #fff;
  cursor: pointer;
}

.deleteBtn {
  height: 25px;
  width: 25px;
  position: relative;
  top: -55px;
  left: 230px;
  border: none;
  font-size: 17px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  float: right;
}

.deleteBtn_img {
  height: 100%;
  width: 100%;
  background: #fff;
}

.plusIcon {
  width: 25px;
  height: 25px;
}

.tech {
  margin: 1% 0.5%;
  padding: 1% 0.5%;
  border-radius: 70px;
  transition: 0.4s ease;
  font-weight: 700;
  letter-spacing: 1.3px;
  text-shadow: 0 0 0.2em #faf7f7;
  text-align: center;
  position: relative;
}

.deleteTech {
  position: absolute;
  width: 100%;
  text-align: center;
  padding: 10px 20px;
  display: none;
}
.deleteTech button {
  cursor: pointer;
  font-size: 20px;
  filter: blur(0.007em);
  font-weight: 700;
  letter-spacing: 1.3px;
  text-shadow: 0 0 0.5em #faf7f7;
  color: #fff;
  display: block;
  position: absolute;
  right: -2px;
  top: -2px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(46, 46, 46, 0.534);
  box-shadow: 0 0 0.8em #f8f7f7;
}
.tech:hover .deleteTech {
  display: block;
}
@media only screen and (max-width: 1500px) {
  .tech {
    width: 20%;
  }
}
.tech:hover {
  transform: scale(1.02);
  box-shadow: 0 10px 25px 0px rgb(107, 129, 158);
  text-shadow: 0 0 0.6em #faf7f7;
}
.tech:nth-child(5n),
.tech:nth-child(5n + 4) {
  background: rgba(104, 48, 145, 0.7);
}
.tech:nth-child(5n + 1),
.tech:nth-child(5n + 3) {
  background: rgba(12, 77, 159, 0.7);
}
.tech:nth-child(5n + 2),
.tech:nth-child(5n + 3) {
  background: rgba(0, 166, 82, 0.7);
}
.tech:nth-child(5n + 3),
.tech:nth-child(5n + 2) {
  background: rgba(239, 76, 45, 0.7);
}
.tech:nth-child(5n + 4),
.tech:nth-child(5n + 1) {
  background: rgba(255, 102, 0, 0.7);
}
.techInitial {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.4);
  text-align: center;
  line-height: 150px;
  font-size: 60px;
  font-weight: bold;
  color: #fefefe;
  margin: 20px auto;
}
.techTitle {
  width: 100%;
  height: 100%;
  display: flex;
  border-radius: 70px;
  /* background: rgba(4, 54, 110, 0.5); */
  font-size: 20px;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1rem;
  text-decoration: none;
  /* text-shadow: 0px 0px 100px 0px #ccc; */
}
