.sidebar {
  width: 64px;
  height: 100vh;
  padding: 20px 0;
  transition: 0.6s ease-in-out;
  position: relative;
}
.sidebarComponent li {
  height: 60px;
}
.menuBtn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 5px;
  margin: 10px;
  overflow: hidden;
  outline: none;
}
.sidebarOpen {
  width: 220px;
  height: 100vh;
  padding: 20px 0;
  transition: 0.6s ease-in-out;
  overflow: hidden;
  position: relative;
}
.sidebarComponent span {
  color: white;
  font-weight: bold;
  line-height: 60px;
  font-size: 14px;
  transition: 0.2s ease-in-out;
}
.sidebarOpen li a {
  width: 220px;
  list-style: none;
  display: flex;
  transition: 0.6s ease-in-out;
  text-decoration: none;
}
.sidebarIcon {
  width: 24px;
  margin: 3px auto;
}
span.versionText {
  color: white;
  padding: 0 10px;
  line-height: 40px;
  font-weight: 100;
  width: 100%;
  font-size: 11px;
}
.sidebarFooter {
  position: absolute;
  bottom: 0px;
  padding: 10px;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  left: 5%;
  justify-content: space-between;
}
.sidebarUpFooter {
  position: absolute;
  bottom: 60px;
  padding: 10px;
  width: 90%;
  left: 0%;
}
.visible {
  opacity: 1;
}
.invisible {
  opacity: 0;
}

.footerBtn {
  margin-right: 30px;
  margin-left: 10px;
}
.icon {
  width: 20px;
}
.itemClass {
  text-decoration: none;
  position: relative;
}
.itemClass:hover {
  cursor: pointer;
}

.chosen {
  position: relative;
  background: #138496 !important;
}

.changeStyle {
  width: 90%;
  margin: 20px auto;
}
.nightdiv {
  background: #263544;
  color: #fff;
}
.daydiv {
  background: #fff;
  color: #263544;
}
.nighttext {
  color: #fff;
}
.daytext {
  color: #263544 !important;
}
button {
  background: transparent;
  border: none;
  cursor: pointer;
}
