.modalWrapper {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.none {
  display: none;
}
.cancel {
  position: absolute;
  padding: 5px 12px;
  right: -10px;
  top: -18px;
  background: rgba(250, 250, 250);
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
}
.cancel button {
  color: #000 !important;
}

.modal {
  width: 450px;
  height: auto;
  background-color: #fff;
  position: absolute;
  top: 15%;
  left: calc(50% - 225px);
  opacity: 1;
  z-index: 10;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
}
