.LatestBox {
  display: flex;
  justify-content: space-between;
}
.Container {
  width: 32%;
  height: max-content;
  margin: 20px 40px;
  display: flex;
  justify-content: space-between;
}
.LatestContainer {
  width: 100%;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 5px 0 rgba(43, 43, 43, 0.1), 0 11px 6px -7px rgba(43, 43, 43, 0.1);
  box-shadow: 0 0 5px 0 rgba(43, 43, 43, 0.1), 0 11px 6px -7px rgba(43, 43, 43, 0.1);
  border: none;
  margin-bottom: 30px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background: #fff;
  padding-bottom: 20px;
}
.LatestHeader {
  background-color: transparent;
  border-bottom: none;
  padding: 20px 20px 0;
  position: relative;
}
.LatestHeader h3 {
  font-size: 20px;
}

.ListItem {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
}
.ListItemLeft {
  width: 70px;
  position: relative;
}
.ListItemLeft::after {
  content: '';
  position: absolute;
  background: rgba(0, 0, 0, 0.2);
  width: 2px;
  height: 100%;
  left: calc(50% - 1px);
  z-index: 1;
}
.ListItemLeft::before {
  content: '';
  position: absolute;
  background: #4099ff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  z-index: 2;
}
.ListItemRight {
  padding: 10px;
}
.ListItemRight h3 {
  max-width: 200px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ListItemRight h4 {
  font-size: 16px;
}
.ListItemRight h3 {
  font-size: 13px;
  color: #868e96;
}
.Hr {
  width: 100px;
  margin-left: 20px;
}
