* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.createProfile {
  margin: 20px 50px;
}
.project-title {
  color: #138496;
  font-weight: bolder;
  font-size: 20px;
}
.project-title:hover {
  text-decoration: none;
}

.card-red {
  background: #ff5370 !important;
}
.card-blue {
  background: #4099ff !important;
}
.card-green {
  background: #2ed8b6 !important;
}
.card-yellow {
  background: #ffb64d !important;
}
.shortlisted,
.blacklisted {
  position: relative;
  padding-left: 15px;
}
.shortlisted::after,
.blacklisted::after {
  content: '';
  position: absolute;

  width: 10px;
  height: 100%;
  left: 0;
  top: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}
.shortlisted::after {
  background: #2ed8b6;
}
.blacklisted::after {
  background: #ff5370;
}
.ReactTable .rt-tbody {
  overflow: hidden !important;
}
